import React from "react";
import { withStyles } from "material-ui/styles";
import { connect } from "@cerebral/react";
import { state, signal } from "cerebral/tags";
import MenuBar from "../MenuBar";
import Connections from "../Connections";
import SideDrawer from "../SideDrawer";
import MapPanel from "../MapPanel";
import BottomDrawer from "../BottomDrawer";
import EditFieldDrawer from "../EditFieldDrawer";
import StatsTable from "../StatsTable";
//import StatsBar from '../StatsBar';
//import InfoBar from '../InfoBar';
//import MenuList from '../MenuList';

const styles = theme => ({
  root: {
    width: "100vw",
    height: "100vh",
    marginTop: theme.spacing.unit * 0,
    zIndex: 1
    //overflow: "hidden"
  },

  app: {
    display: "flex",
    flexDirection: "column",
    height: "100vh",
    zIndex: 1,
    //overflow: "hidden",
    //position: 'fixed',
    top: 0
    //paddingTop: 56
  }
});

/**
 *
 */
class App extends React.Component {
  componentWillMount() {
    this.props.init({});
    this.props.loadFieldListData({ switchTab: 0 });
  }

  render() {
    const { classes } = this.props; // , theme

    return (
      <div className={classes.app}>
        <MenuBar />
        <Connections />
        <SideDrawer />
        <MapPanel />
        <BottomDrawer />
        {this.props.fieldEditing ? <EditFieldDrawer /> : null}
      </div>
    );
  }
}

export default connect(
  {
    fieldEditing: state`fields.editing`,
    init: signal`App.init`,
    loadFieldListData: signal`FldList.loadData`
  },
  withStyles(styles, { withTheme: true })(App)
);
