import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import { connect } from '@cerebral/react';
import {signal, state} from 'cerebral/tags';

const styles = theme => ({
    flexContainer: {
        display: 'flex',
        marginTop: theme.spacing.unit * 1,
        backgroundColor: theme.palette.common.black,
        color: theme.palette.common.white,
        align: 'center',
        justifyContent: 'center',
        alignItems: 'center'
        // overflowX: 'auto'
    },
    completed: {
        textAlign: 'center',
        height: 60,
        width: 100,
        margin: '10px',
        backgroundColor: '#66BB6A',
        color: '#FFFFFF',
        alignItems: 'center',
        borderRadius: '16px'
    },
    started: {
        textAlign: 'center',
        height: 60,
        width: 100,
        margin: '10px',
        backgroundColor: '#FFC107',
        color: '#FFFFFF',
        alignItems: 'center',
        borderRadius: '16px'
    },
    planned: {
        textAlign: 'center',
        height: 60,
        width: 100,
        margin: '10px',
        backgroundColor: 'red',
        color: '#FFFFFF',
        alignItems: 'center',
        borderRadius: '16px'
    }
});

class StatsTable extends React.Component {

    render() {
        const { classes } = this.props;

        return (

            <div className={classes.flexContainer}>
                <div className={classes.completed}>
                    {  "Completed " +
                       this.props.OpsListCompletedArea + " ac (" +
                       Math.round(this.props.OpsListCompletedPercentage) + "%)"
                    }
                </div>

                <div className={classes.started}>
                    {  "Started " +
                        this.props.OpsListStartedArea + " ac (" +
                        Math.round(this.props.OpsListStartedPercentage) + "%)"
                    }
                </div>

                <div className={classes.planned}>
                    {  "Planned " +
                        this.props.OpsListPlannedArea + " ac (" +
                        Math.round(this.props.OpsListPlannedPercentage) + "%)"
                    }
                </div>
            </div>

        );

    }//render
}

StatsTable.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default connect({
        OpsListCompletedArea: state`OpsList.completed.area`,
        OpsListCompletedPercentage: state`OpsList.completed.percentage`,
        OpsListCompletedTotalFields: state`OpsList.completed.totalFields`,

        OpsListStartedArea: state`OpsList.started.area`,
        OpsListStartedPercentage: state`OpsList.started.percentage`,
        OpsListStartedTotalFields: state`OpsList.started.totalFields`,

        OpsListPlannedArea: state`OpsList.planned.area`,
        OpsListPlannedPercentage: state`OpsList.planned.percentage`,
        OpsListPlannedTotalFields: state`OpsList.planned.totalFields`,

        handleUpdateAction: signal`StatsTable.handleUpdateAction`,

        handleCloseCompleted: signal`StatsTable.handleCloseCompleted`,
        handleCloseStarted: signal`StatsTable.handleCloseStarted`,
        handleClosePlanned: signal`StatsTable.handleClosePlanned`
    },
    withStyles(styles, { withTheme: true })(StatsTable)
);