import React from "react";
import PropTypes from "prop-types";
import { connect } from "@cerebral/react";
import { state, signal } from "cerebral/tags";
import { withStyles } from "material-ui/styles";
import Drawer from "material-ui/Drawer";
import TextField from "material-ui/TextField";
import Radio, { RadioGroup } from "material-ui/Radio";
import Icon from "material-ui/Icon";
import { FormControl, FormControlLabel } from "material-ui/Form";
import Typography from "material-ui/Typography";
import Button from "material-ui/Button";
import { MenuItem } from "material-ui/Menu";
import "react-select/dist/react-select.css";
import EditFieldIcon from "material-ui-icons/Edit";
import IconButton from "material-ui/IconButton";

const bottomDrawerHeight = 200;

const styles = theme => ({
  formControl: {
    margin: theme.spacing.unit * 2,
    flexWrap: "noWrap",
    justifyContent: "flex-start"
  },
  group: {
    display: "flex",
    flex: 1,
    flexDirection: "row",
    flexWrap: "noWrap",
    justifyContent: "center"
  },
  statusButton: {
    display: "flex",
    flexDirection: "column",
    justifyContent: "center",
    fontColor: "white",
    margin: 0, //theme.spacing.unit * 2,
    padding: theme.spacing.unit
  },
  container: {
    display: "flex",
    flexDirection: "column",
    flexWrap: "noWrap",
    justifyContent: "center",
    alignItems: "center",
    padding: theme.spacing.unit,
    backgroundColor: "#212121",
    minWidth: 0,
    order: 3
  },
  middle: {
    display: "flex",
    alignItems: "center"
  },
  editFieldButton: {
    color: "white"
  },
  textField: {
    display: "flex",
    flexDirection: "row",
    justifyContent: "flex-start",
    //marginLeft: theme.spacing.unit * 2,
    marginRight: theme.spacing.unit,
    minWidth: "200px",
    width: "20%",
    color: "#FAFAFA",
    rightPadding: 10
  },
  drawerPaper: {
    display: "flex",
    height: bottomDrawerHeight
  },
  drawerHeader: {
    display: "flex",
    flexDirection: "row",
    flexGrow: 2,
    alignItems: "center",
    backgroundColor: "#3f51b5",
    color: "white",
    justifyContent: "space-between",
    padding: "0 8px",
    height: "10%",

    ...theme.mixins.toolbar
  },
  confirmIconGroup: {
    display: "flex",
    flexDirection: "row",
    justifyContent: "flex-end",
    flexGrow: 2,
    flexWrap: "noWrap"
  },
  form: {
    color: "#FAFAFA",
    display: "flex",
    flexDirection: "row",
    justifyContent: "space-between",
    alignItems: "center",
    width: "90%",
    flexWrap: "wrap"
  }
});

class BottomDrawer extends React.Component {
  renderDatePicker() {
    const { classes } = this.props;

    return (
      <TextField
        id="date"
        label="Date"
        type="date"
        className={classes.textField}
        value={this.props.currentTimestamp}
        //className={classes.textField}
        InputLabelProps={{
          shrink: true,
          style: { color: "white" }
        }}
        InputProps={{
          style: { color: "white" }
        }}
        onChange={event => {
          this.props.setCurrentSelectionStatusTimestamp({
            timestamp: event.target.value
          });
        }}
      />
    );
  } //renderDatePicker

  renderEditField() {
    const { classes } = this.props;

    return (
      <IconButton
        key={4}
        color="inherit"
        aria-label="open drawer"
        onClick={() => this.props.editFieldClicked({})}
        className={classes.editFieldButton}
      >
        <EditFieldIcon />
      </IconButton>
    );
  } //renderEditField

  renderAddOperationForm() {
    const { classes } = this.props;

    return (
      <form className={classes.container} noValidate>
        <div className={classes.form}>
          <TextField
            id="operation name"
            label="New Operation Name:"
            className={classes.textField}
            value={this.props.newOperationName}
            onChange={event => {
              this.props.newOperationNameTyped({
                inputText: event.target.value,
                inputType: "operation"
              });
            }}
            margin="normal"
            InputProps={{
              style: { color: "#FAFAFA" },
              classes: {
                input: classes.input
              }
            }}
            InputLabelProps={{ style: { color: "white" }, shrink: true }}
          />
        </div>
        <div>
          <Button
            variant="raised"
            className={classes.button}
            color="primary"
            style={{ color: "#FAFAFA" }}
            disabled={this.props.disabledNewOperation}
            onClick={() => {
              this.props.addNewOperation();
            }}
          >
            Add
          </Button>
          <Button
            variant="raised"
            className={classes.button}
            color="secondary"
            style={{ color: "#FAFAFA" }}
            onClick={() => {
              this.props.cancelNewOperation();
            }}
          >
            Cancel
          </Button>
        </div>
      </form>
    );
  } //addOperationForm

  renderAddFieldForm() {
    const { classes } = this.props;

    return (
      <form className={classes.container} noValidate>
        <div className={classes.form}>
          <TextField
            id="field name"
            label="New Field Name:"
            className={classes.textField}
            value={this.props.newFieldName}
            onChange={event => {
              this.props.newFieldNameTyped({
                inputText: event.target.value,
                inputType: "field"
              });
            }}
            margin="normal"
            InputProps={{
              style: { color: "#FAFAFA" },
              classes: {
                input: classes.input
              }
            }}
            InputLabelProps={{ style: { color: "white" }, shrink: true }}
          />
        </div>
        <div>
          <Button
            variant="raised"
            className={classes.button}
            color="primary"
            style={{ color: "#FAFAFA" }}
            disabled={this.props.disabledNewOperation}
            onClick={() => {
              this.props.addNewField();
            }}
          >
            Add
          </Button>
          <Button
            variant="raised"
            className={classes.button}
            color="secondary"
            style={{ color: "#FAFAFA" }}
            onClick={() => {
              this.props.cancelNewOperation();
            }}
          >
            Cancel
          </Button>
        </div>
      </form>
    );
  } //addOperationForm

  renderRadioButtonGroup() {
    const { classes } = this.props;
    const datePicker = this.renderDatePicker({});
    const editField = this.renderEditField({});

    return (
      <form className={classes.container} noValidate>
        <TextField
          select
          fullWidth
          className={classes.textField}
          label={"Operator"}
          InputLabelProps={{
            shrink: true,
            style: { color: "white" }
          }}
          value={this.props.currentMarker}
          onChange={event => {
            this.props.setCurrentSelectionStatusMarker({
              marker: event.target.value
            });
          }}
          InputProps={{
            style: { color: "white" }
          }}
        >
          {Object.values(this.props.allOperators).map(operator => (
            <MenuItem key={operator.label} value={operator.label}>
              {operator.label}
            </MenuItem>
          ))}
        </TextField>
        <div className={classes.middle}>
          <FormControl
            component="fieldset"
            required
            className={classes.formControl}
          >
            <RadioGroup
              aria-label="status"
              name="status"
              className={classes.group}
              value={this.props.currentStatus}
              onChange={event => {
                this.props.setCurrentSelectionStatusStatus({
                  editFieldStatus: true,
                  value: event.target.value,
                  statusValue: event.target.value
                });
              }}
            >
              <FormControlLabel
                value="unplanned"
                className={classes.statusButton}
                control={
                  <Radio
                    checkedIcon={
                      <Icon style={{ color: "#FAFAFA", fontSize: 50 }}>
                        check_circle
                      </Icon>
                    }
                    icon={
                      <Icon style={{ color: "#FAFAFA", fontSize: 50 }}>
                        radio_button_checked
                      </Icon>
                    }
                  />
                }
                label={
                  <Typography style={{ color: "white" }}>Unplanned</Typography>
                }
              />
              <FormControlLabel
                value="planned"
                className={classes.statusButton}
                control={
                  <Radio
                    checkedIcon={
                      <Icon style={{ color: "red", fontSize: 50 }}>
                        check_circle
                      </Icon>
                    }
                    icon={
                      <Icon style={{ color: "red", fontSize: 50 }}>
                        radio_button_checked
                      </Icon>
                    }
                  />
                }
                label={
                  <Typography style={{ color: "white" }}>Planned</Typography>
                }
              />
              <FormControlLabel
                value="started"
                className={classes.statusButton}
                control={
                  <Radio
                    checkedIcon={
                      <Icon style={{ color: "#FFC107", fontSize: 50 }}>
                        check_circle
                      </Icon>
                    }
                    icon={
                      <Icon style={{ color: "#FFC107", fontSize: 50 }}>
                        radio_button_checked
                      </Icon>
                    }
                  />
                }
                label={
                  <Typography style={{ color: "white" }}>Started</Typography>
                }
              />
              <FormControlLabel
                value="completed"
                className={classes.statusButton}
                control={
                  <Radio
                    checkedIcon={
                      <Icon style={{ color: "#76FF03", fontSize: 50 }}>
                        check_circle
                      </Icon>
                    }
                    icon={
                      <Icon style={{ color: "#76FF03", fontSize: 50 }}>
                        radio_button_checked
                      </Icon>
                    }
                  />
                }
                label={
                  <Typography style={{ color: "white" }}>Completed</Typography>
                }
              />
            </RadioGroup>
          </FormControl>
          {editField}
        </div>
        {datePicker}
      </form>
    );
  } //renderRadioButtonGroup

  renderHeaderItem() {
    const { classes } = this.props;

    if (this.props.bottomMenu === "operations") {
      let headerText = "Add New Operation";
      return (
        <div
          className={classes.drawerHeader}
          tabIndex={0}
          role="banner"
          onClick={() => {
            this.props.cancelNewOperation({});
          }} /*{this.toggleDrawer(false)}*/
          onKeyDown={() => {
            this.props.cancelNewOperation({});
          }} /*{this.toggleDrawer(false)}*/
        >
          <div>{headerText}</div>
        </div>
      );
    } else {
      if (this.props.bottomMenu === "status-update") {
        let headerText =
          "Updating status for operation [" +
          this.props.currentOperationLabel +
          "] on field " +
          this.props.currentFieldLabel;
        return (
          <div
            className={classes.drawerHeader}
            tabIndex={0}
            role="banner"
            onClick={() => {
              this.props.saveCurrentSelectionStatus({});
            }} /*{this.toggleDrawer(false)}*/
            onKeyDown={() => {
              this.props.saveCurrentSelectionStatus({});
            }} /*{this.toggleDrawer(false)}*/
          >
            <div>{headerText}</div>
          </div>
        );
      }
    }
  } //renderHeaderItem

  render() {
    //const { classes } = this.props;
    //const datePicker = this.renderDatePicker({});
    const header_item = this.renderHeaderItem({});
    const add_operation_form = this.renderAddOperationForm({});
    //const add_field_form = this.renderAddFieldForm({});
    const status_radio_buttons = this.renderRadioButtonGroup({});

    return (
      <Drawer
        anchor="bottom"
        open={this.props.bottomDrawerOpen}
        onClose={() => {
          this.props.saveCurrentSelectionStatus({});
        }} /*{this.toggleDrawer(false)}*/
      >
        {header_item}
        {this.props.bottomMenu === "status-update"
          ? status_radio_buttons
          : this.props.bottomMenu === "operations"
          ? add_operation_form
          : null}
      </Drawer>
    );
  }
}

BottomDrawer.propTypes = {
  classes: PropTypes.object.isRequired,
  theme: PropTypes.object.isRequired
};

export default connect(
  {
    bottomDrawerOpen: state`BottomDrawer.open`,
    bottomMenu: state`BottomDrawer.menu`,

    currentStatus: state`User.currentSelection.status.status`,
    currentMarker: state`User.currentSelection.status.marker`,
    currentTimestamp: state`User.currentSelection.status.timestamp`,
    allOperators: state`User.accessibleUsers`,
    operatorMenuOpen: state`BottomDrawer.operatorMenuOpen`,

    currentFieldLabel: state`fields.records.${state`fields.selectedId`}.name`,
    currentOperationLabel: state`operations.records.${state`User.currentSelection.operation.id`}.label`,
    newOperationName: state`User.currentSelection.operation.inputText`,
    disabledNewOperation: state`User.currentSelection.operation.disabled`,

    handleBottomDrawerOpen: signal`BottomDrawer.handleDrawerOpen`,

    setCurrentSelectionStatusStatus: signal`User.setCurrentSelectionStatusStatus`,
    setCurrentSelectionStatusMarker: signal`User.setCurrentSelectionStatusMarker`,
    setCurrentSelectionStatusTimestamp: signal`User.setCurrentSelectionStatusTimestamp`,
    saveCurrentSelectionStatus: signal`User.saveCurrentSelectionStatus`,
    resetCurrentSelectionStatus: signal`User.resetCurrentSelectionStatus`,
    handleOperatorMenuOpen: signal`BottomDrawer.handleOperatorMenuOpen`,
    handleOperatorMenuClose: signal`BottomDrawer.handleOperatorMenuClose`,

    newOperationNameTyped: signal`AutocompleteSearch.operationInputTyped`,
    addNewOperation: signal`User.addNewOperation`,
    setDisabledNewOperation: signal`User.setDisabledNewOperation`,
    cancelNewOperation: signal`User.cancelNewOperation`,

    editFieldClicked: signal`AutocompleteSearch.handleFieldEdited`
  },
  withStyles(styles, { withTheme: true })(BottomDrawer)
);
